import React, { Component } from 'react';

import { withStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import Chips from '../component/Chips';
import Divider from '@material-ui/core/Divider';
import Loader from 'react-loader-spinner'

const styles = ({ palette }) => {
    return {
        root: {
            height: '100%',
            display: 'flex',
            flexFlow: 'column'
        },
        positionsHeader: {
            flex: '0 0',
            '& > :first-child': {
                display: 'flex',
                alignItems: 'flex-end',
                '& h1': {
                    marginTop: 0,
                    marginBottom: 0,
                    flex: '1 100%'
                }
            }
        },
        subheader: {
            margin:'auto', 
            padding: '15px',
            color: 'grey'
        },
        button: {
            height: 23,
            alignSelf:'flex-end',
            color: 'grey'
        },
        textField: {
            marginTop: '0.5em',
            marginBottom: '1em',
            marginLeft: '0.5em',
            marginRight: '0.5em',
            width: 200,
        },
        negativeMetric: {
            fontSize: '1em',
            color: 'grey'
        }
    };
};

const hashtags = ["#dailychallenge", "#afinnishwordaday","#sketch", "#type", "#design", "#designer",  "#typeface", "#lettering",  "#finnish", "#language", "#typography", "#typesketch", "#typedesign", "#practice", "#grid"]
class StyleRecognizerWidget extends Component {
    state={
        render: false //Set render state to false
    };

    componentDidMount() {
      setTimeout(function() { //Start the timer
          this.setState({render: true}) //After 1 second, set render to true
      }.bind(this), 800)
    }
    render() {
        const { classes } = this.props;

        return (
            <Card className={classes.root}>
                <div className={classes.positionsHeader}>
                    <div>
                        <h2 className={classes.subheader}>Suggested Hashtags</h2>
                    </div>
                    <Divider/>
                    {this.state.render?
                        (<div align="center" style={{backgroundColor:'#eeeeee'}}>
                        <Chips hashtags={hashtags}/>
                        </div>):   
                        <div style={{margin:'auto',display:'table',marginTop:'25%'}}>     
                         <Loader 
                         type="Grid"
                         color="grey"
                         height="100"   
                         width="100"
                        />   
                        </div>
                    }
                </div>

            </Card>
        );
    }
}

export default withStyles(styles)(StyleRecognizerWidget);
