import React, { Component } from 'react'; 
import Tab from '@material-ui/core/Tab';
import Tabs from '@material-ui/core/Tabs';

const PARENT_TAB = ['Home','Gallery'];
const CHILD_TAB = {
    'Gallery':['Season1', 'Season2'],
    'Configuration':['Implentation','Service Upgrade', 'Service Registry']
};

class Navigation extends Component {
    state = {       
        secondaryTab: [],
        value:'Home',
        secondaryValue:'Season1'
    }

    handleOnChange = (_, value) => {
        if (value==='Transactions' || value==='On-Boarding') {
            this.setState({secondaryValue: 'Client'})
        }
        if (value==='Configuration') {
            this.setState({secondaryValue: 'Service Registry'})
        }
        this.setState({
            'secondaryTab': CHILD_TAB[value],
            value: value
        });
        this.props.onTabUpdate(value);
    };

    handleSecondaryOnChange =(_,value) => {
        this.setState({secondaryValue: value});
        this.props.onTabUpdate(value);
    };

    renderParentNav = () => {
        return (
            PARENT_TAB.map((d)=>{
                let key = 'primary-tab-'+d;
                if(d==='Transactions'){
                    return <Tab value={d} label={d} key={key} />;
                } else {
                    return <Tab value={d} label={d} key={key} />;
                }})
        );
    }

    renderSecondaryNav = () => {
        if (this.state&&this.state.secondaryTab&&this.state.secondaryTab.length !== 0) {

            return (
                <div style={{'backgroundColor':'black', 'color':'white'}}>
                    <Tabs
                        centered
                        value={this.state.secondaryValue}
                        onChange={this.handleSecondaryOnChange}
                    >
                    {this.state.secondaryTab.map((d)=>{
                        let key = 'secondary-nav-'+d;
                        return (<Tab key={key} value={d} label={d} />);
                    })}
                    </Tabs>
                </div>);
        }
    }
    render() {
        return (
            <div style={{backgroundColor:'black'}}>
                <div style={{backgroundColor:'black', 'color':'white'}}>
                    <span style={{float: 'left', marginLeft:'12px', marginTop:'12px'}}>TYPE LAB</span>
                        <Tabs
                            centered
                            value={this.state.value}
                            onChange={this.handleOnChange}
                        >
                            {this.renderParentNav()}
                        </Tabs>
                        {this.renderSecondaryNav()}
                    </div>         
            </div>
            );
    };
}

export default Navigation;
