import React, { Component } from 'react';
import styled from 'styled-components';
import ReactGridLayout from 'react-grid-layout';
import {WidthProvider} from 'react-grid-layout';
import RecognizerWidget from '../widget/RecognizerWidget';
import StyleRecognizerWidget from '../widget/StyleRecognizerWidget';
import SimilarRetrievalWidget from '../widget/SimilarRetrievalWidget';
import Card from '@material-ui/core/Card';
import  Button  from '@material-ui/core/Button';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Typography from '@material-ui/core/Typography';
const GridLayout = WidthProvider(ReactGridLayout);
const Grid = styled(GridLayout)`
  margin: 0px 0px 0px;
  background-color: ${({ theme }) => theme.secondaryBackground};
`;

// layout is an array of objects, see the demo for more complete usage
const layout = [
    {i: 'upload', x: 0, y: 0, w: 1, h: 4, static: false},
    {i: 'obc', x: 1, y: 0, w: 3, h: 4, static: false},
    {i: 'csp', x: 4, y: 0, w: 4, h: 4, static: false},
    {i: 'findsimilar', x: 0, y: 6, w: 12, h: 6, static: false},
    {i: 'recognizestyle', x: 8, y: 0, w: 4, h: 4, static: false},

];


class RecognizerPage extends Component {
    state = {
      file: null,
      title: null,
      showHashtag: false,
      showStyle: false,
      showSimilar: false
    }
    handleChange=(event) => {
        this.setState({
          file: URL.createObjectURL(event.target.files[0]),
          title:event.target.files[0].name.split(".")[0]
        })
    };
    handleRecognize=()=>this.setState({showHashtag:!this.state.showHashtag});
    
    handleStyle=()=>this.setState({showStyle:!this.state.showStyle});

    handleMatch=()=>this.setState({showSimilar:!this.state.showSimilar});


    getCard=()=>{
        if (this.state.file) {
            return (   
             <Card>
              <CardActionArea>
                <CardMedia
                  style={{height: 200}}
                  image={this.state.file}
                  title={this.state.title}
                />
                <CardContent>
                  <Typography gutterBottom variant="h5" component="h2">
                    {this.state.title}
                  </Typography>
                </CardContent>
              </CardActionArea>
              <CardActions>
                <Button size="small" color="primary" onClick={this.handleRecognize}>
                  Recognize Style
                </Button>
                <Button size="small" color="primary" onClick={this.handleStyle}>
                  Generate Tags
                </Button>
                <Button size="small" color="primary" onClick={this.handleMatch}>
                 Match
                </Button>
              </CardActions>
            </Card>       );
        }
    }
	render() {

		return (

        <Grid  layout={layout} cols={12} rowHeight={(window.innerHeight - 29) / 10}  width={1200}>
            <div key="upload"  background-color="black">
                    <div align="center" >
                    <input
                      accept="image/*"
                      style={{ display: 'none' }}
                      id="raised-button-file"
                      multiple
                      type="file"
                      onChange={this.handleChange}
                    />
                    <label htmlFor="raised-button-file">
                      <Button variant="raised" component="span" style={{height:23, alignSelf:'flex-end'}}>
                        Upload
                      </Button>
                    </label> 
                    </div>
            </div>
            <div key="obc" >
                {this.getCard()}     
            </div>
            <div key="csp">
                {this.state.showHashtag?(<RecognizerWidget />):(<div></div>)}
            </div>
            <div key="recognizestyle">
                {this.state.showStyle?(<StyleRecognizerWidget />):(<div></div>)}
            </div>
            <div key="findsimilar">
                {this.state.showSimilar?(<SimilarRetrievalWidget />):(<div></div>)}
            </div>
        </Grid>
        );
	}
}

export default RecognizerPage;
