import React, { Component } from 'react';
// import MuiThemeProvider from '@material-ui/core/styles/MuiThemeProvider';
import { withStyles } from '@material-ui/core/styles';
import Navigation from './component/Navigation';
import { MuiThemeProvider, createMuiTheme } from '@material-ui/core/styles';

import Paper from '@material-ui/core/Paper';
// import theme from './metric/theme'
import ShowcasePage from './page/ShowcasePage';
import HomePage from './page/HomePage';
import LoginPage from './page/LoginPage';
import RecognizerPage from './page/RecognizerPage';
import ShowcasePageSeason2 from './page/ShowcasePageSeason2';
const styles = () => {

    return {
        root: {
            display: 'flex',
            flexFlow: 'column',
            height: '100%'
        },

        content: {
            flex: '1',
            display: 'flex'
        },
        sidebar: {
            flex: '0 320px'
        },
        mainContent: {
            flex: '1',
            'background-color': '#eeeeee',
            'padding': '0px',
            overflowY:'auto'
        },
        utilityNav: {
            height: '100%',
            marginRight: '24px'
        },
        '@media (max-width:960px)': {
            sidebar: {
                display: 'none'
            }
        }
    };
};

const theme = createMuiTheme();



class App extends Component { 
  onTabUpdate = (val) => {
      this.setState({currentTab: val});
  };
  state = {
      currentTab: 'Home'
  }
  renderPageContent = () => {
    console.log('this.state.currentTab');
      switch (this.state.currentTab) {
          case 'Gallery':
              return <ShowcasePage />;
          case 'Home':
              return <HomePage />;
          case 'Recognizer':
              return <RecognizerPage/>;
          case 'LoginPage':
            return <LoginPage onTabUpdate={this.onTabUpdate}/>
          case 'Season1':
            return <ShowcasePage />
          case 'Season2':
            return <ShowcasePageSeason2 />
          default:
              return <HomePage />
      }
  }

  render() {
      const { classes } = this.props;
      return(
          <div className={classes.root}>
          <MuiThemeProvider theme={theme}>
              <Navigation onTabUpdate={this.onTabUpdate} currentTab={this.state.currentTab}/>
              <div className={classes.content}>
                  <Paper className={classes.mainContent}>
                      {this.renderPageContent()}
                  </Paper>
              </div>
          </MuiThemeProvider>
          </div>);
      };
};

export default withStyles(styles)(App);
