import React, { Component } from 'react';

import { withStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import Divider from '@material-ui/core/Divider';
import ReactGridLayout from 'react-grid-layout';
import {WidthProvider} from 'react-grid-layout';
import styled from 'styled-components';
import ImageCard from '../component/ImageCard';
import data from '../data/data.js';
import Loader from 'react-loader-spinner'

const GridLayout = WidthProvider(ReactGridLayout);
const Grid = styled(GridLayout)`
  margin: 0px 0px 0px;
  background-color: ${({ theme }) => theme.secondaryBackground};
`;
const styles = ({ palette }) => {
    return {
        root: {
            height: '100%',
            display: 'flex',
            flexFlow: 'column'
        },
        positionsHeader: {
            flex: '0 0',
            '& > :first-child': {
                display: 'flex',
                alignItems: 'flex-end',
                '& h1': {
                    marginTop: 0,
                    marginBottom: 0,
                    flex: '1 100%'
                }
            }
        },
        subheader: {
            margin:'auto', 
            padding: '15px',
            color: 'grey'
        },
        button: {
            height: 23,
            alignSelf:'flex-end',
            color: 'grey'
        },
        textField: {
            marginTop: '0.5em',
            marginBottom: '1em',
            marginLeft: '0.5em',
            marginRight: '0.5em',
            width: 200,
        },
        negativeMetric: {
            fontSize: '1em',
            color: 'grey'
        }
    };
};
const layout = [
    {i: 'a', x: 0, y: 0, w: 3, h: 5, static: false},
    {i: 'b', x: 3, y: 0, w: 3, h: 5, static: false},
    {i: 'c', x: 6, y: 0, w: 3, h: 5, static: false},
    {i: 'd', x: 9, y: 0, w: 3, h: 5, static: false},
];
class SimilarRetrievalWidget extends Component {
    state={
        render: false //Set render state to false
    };

    componentDidMount() {
      setTimeout(function() { //Start the timer
          this.setState({render: true}) //After 1 second, set render to true
      }.bind(this), 500)
    }
    render() {
        const { classes } = this.props;

        return (
            <Card className={classes.root}>
                <div className={classes.positionsHeader}>
                    <div>
                        <h2 className={classes.subheader}>Similarity Matching</h2>
                    </div>
                    <Divider/>
                    {this.state.render?(
                    <Grid  layout={layout} cols={12} rowHeight={(window.innerHeight - 29) / 10}  width={1200}>
                            <div key="a" ><ImageCard url={data[4]['img']} title={'data1'} /></div>
                            <div key="b" ><ImageCard url={data[52]['img']} title={'data1'} /></div>
                            <div key="c" ><ImageCard url={data[22]['img']} title={'data1'} /></div>
                            <div key="d" ><ImageCard url={data[42]['img']} title={'data1'} /></div>
                     </Grid>):
                    <div style={{margin:'auto',display:'table',marginTop:'25px'}}>     
                         <Loader 
                             type="Grid"
                             color="grey"
                             height="100"   
                             width="100"
                        />   
                    </div>
                }

                </div>

            </Card>
        );
    }
}

export default withStyles(styles)(SimilarRetrievalWidget);
