import React, { Component } from 'react';
import styled from 'styled-components';
import LoginWidget from '../widget/LoginWidget';
import ReactGridLayout from 'react-grid-layout';
import {WidthProvider} from 'react-grid-layout';

const GridLayout = WidthProvider(ReactGridLayout);
const Grid = styled(GridLayout)`
  margin: 0px 0px 0px;
  background-color: ${({ theme }) => theme.secondaryBackground};
`;
const layout = [
    {i: 'black-list', x: 4, y: 1, w: 4, h: 6,  static: true},
];

const clients = [
    {
        value: 'WMU',
        label: 'Walmart USA',
    },
    {
        value: 'CTHY',
        label: 'Cathey Pacific',
    },
    {
        value: 'TD',
        label: 'TD Bank',
    },
    {
        value: 'JPY',
        label: 'Japan Mobile',
    },
];
class LoginPage extends Component {
    handleClick = () => {
        this.props.onTabUpdate('Transactions');
    }
	render() {
		return (
            <Grid  layout={layout} cols={12} rowHeight={(window.innerHeight - 29) / 10}  width={1200}>
                <div key="black-list">
                    <LoginWidget title={"Login"} dataList={clients} buttonName={"Confirm"} handleClick={this.handleClick}/>
                </div>
            </Grid>
        );
	}
}

export default LoginPage;
